

.recharts-container {
    display: inline-block;
    margin-left: 2rem;
    margin-right: 2rem;
}
.recharts-wrapper {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

// Axes text
.recharts-text.recharts-cartesian-axis-tick-value{
    font-size: 1.2rem;
}


