
$slate: #434f62;
$battleship: #687284;
$watermelon: #336699;//#fb5661;
$watermelonHover: #336699;//#c1262e;
$greyish: #9ca6b7;
$pale: #e1e6eb;
$veryPale: #f2f4f8;
$greenish: #31d883;
$yellowish: #fdaa14;
$clearBlue: #168dff;
$menuBackground: #e8ebf2;

// taken from MUI theme
$themeBackground: #fafafa;
$themeSpacingUnit: 8;

// responsive variables
$xs: 360px;
$sm: 600px;
$md: 960px;
$lg: 1280px;
$xl: 1920px;

@function multiply($value1, $value2) {
  @return $value1 * $value2;
}

* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  height: 100%;
  font-weight: 400;
  color: #687284;

  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  text-shadow: rgba(0, 0, 0, .01) 0 0 1px;

  margin: 0;
  padding: 0;
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;

}

body {
  color: $dark-grey;
  font-family: Helvetica, Arial, sans-serif;
  font-size: $m-size;
  line-height: 1.6;
  height: 100%;
}

button {
  cursor: pointer;
}

button:disabled {
  cursor: default;
}

.is-active {
  font-weight: bold;
}

