.logout-button-container{
  background-color: rgb(232, 235, 242);
  padding-bottom: 16px;
  padding-right: 16px;
  padding-left: 16px;
  margin-top: 50px;
  ::selection {
     background:none;
      color: inherit;
  }

  ::-moz-selection {
      background: none;
      color: inherit;
  }

  ::-webkit-selection {
      background:none;
      color: inherit;
  }
}
.logout-button{
  color: $greyish;
  background-color: $veryPale;
  padding: 4px;
  font-size: 11px;
  line-height: 19px;
  margin-left: 24px;
  cursor: pointer;
  -webkit-transition: all .3s;
  -ms-transition: all .3s;
  -moz-transition: all .3s;
  transition: all .3s;
}
.logout-button:hover{
  color: $battleship;
  background-color: $pale;
}

.background-overlay{
  position:absolute;
  top: 0px;
  left: 0px;
  width: 0px;
  height:0px;
  opacity: 0;
  -webkit-transition: opacity .1s;
  -ms-transition: opacity .1s;
  -moz-transition: opacity .1s;
  transition: opacity .1s;
  button{
    float: right;
  }
}
.background-overlay.menu-open{
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.3);
  opacity: 1;
  z-index: 998;
}

.side-menu-container{
  display: "flex";
  flex-direction: "column";
  box-shadow: "0 0 1px 0 rgba(62, 68, 167, 0.16)";
  -webkit-transition: all .3s;
  @media screen and (max-width: $md) {
    position:absolute;
    top: 0px;
    left:0px;
    z-index: 999;
    min-height: 100vh;
    background-color: $menuBackground;
    // display:none;
    -ms-transform: translate(-240px, 0px); /* IE 9 */
    -webkit-transform: translate(-240px, 0px); /* Safari */
    transform: translate(-240px, 0px);
  }
}

.side-menu-container.menu-open{
  @media screen and (max-width: $md) {
    // display: "flex";
    -ms-transform: translate(0px, 0px); /* IE 9 */
    -webkit-transform: translate(0px, 0px); /* Safari */
    transform: translate(0px, 0px);
  }
}

.side-menu-item{
  color: $greyish;
  cursor: pointer;
  -webkit-transition: color .3s;
  -ms-transition: color .3s;
  -moz-transition: color .3s;
  transition: color .3s;
}
.side-menu-item svg{
  -webkit-transition: fill .3s;
  -ms-transition: fill .3s;
  -moz-transition: fill .3s;
  transition: fill .3s;
  fill: $greyish;
}

.side-menu-icon {
  display: inline-block;
  height: 24px;
  margin-left: 24px;
  margin-right: 24px;
  transform: translateY(4px);
}

.side-menu-item:hover, .side-menu-item.active{
  color: $slate
}
.side-menu-item.active svg{
  fill: $watermelon;
}
.side-menu-item:hover svg{
  fill: $watermelon;
}

.battleship-color{
  color: $battleship;
}

.units-text {
  color: $greyish;
}

// -----------------------------
// <main> Container
// -----------------------------
.main-container {
  width: 100%;
  // background-color: themeBackground;
  // width: "calc(100% - 240px)";
  // padding: multiply($themeSpacingUnit, 3);
  // // Emile's overrides in .content
  // // padding: 0;
  @media screen and (max-width: $md) {
    margin-top: 36px;
  }
}

.menu-toggle{
  position:absolute;
  top: 0px;
  left: 0px;
  @media screen and (min-width: $lg) {
    display: none;
  }
}

// -----------------------------
// Plots
// -----------------------------
.flow-plot-item{
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  @media screen and (min-width: $lg) {
    width: 28%;
    margin-left: "12.5px";
    margin-right: "12.5px";
  }
}

.toggle-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  @media screen and (min-width: $lg) {
    flex-direction: row;
  }
}
.radio-container{
  display: flex;
}
.radio-label{
  display: inline-flex;
  height: 48px;
  align-items: center;

  // display1 copy/paste
  color: rgba(0, 0, 0, 0.54);
  font-size: 12px;
  font-weight: 400;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  line-height: 16px;
}

// -----------------------------
// Selection Bar
// -----------------------------

.selection-bar-container{
  margin-top: 10px;
  @media screen and (min-width: $lg) {
    margin-top: 56px;
  }
}
.well-selection-dropdown {
  cursor: pointer;
}

.brush-marker{
  stroke: $greyish;
  stroke-width: 1;
  stroke-dasharray: 4,4;
  // -webkit-transition: transform .1s; /* Safari */
  // -moz-transition: transform .1s; /* Mozilla */
  // -ms-transition: transform .1s; /* ie? */
  // transition: transform .1s;
}

// Well geometry
.warning-icon{
    fill: $watermelon;
}
.warning-icon:hover{
  fill: $watermelonHover;
}

.md-geometry-circle {
  stroke: $watermelon;
  stroke-width: 1px;
  fill: #fff;
}
.md-geometry-circle-loading {
  stroke: $greyish;
  stroke-dasharray: 2;
  stroke-width: 1px;
  fill: #fff;
}

//Loader animation
// -----------------------------------------
.md-geometry-circle-loading {
  -webkit-transform-origin: 50% 50%;
  -moz-transform-origin: 50% 50%;
  -ms-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  -webkit-animation: rotation 2s infinite linear;
  -moz-animation: rotation 2s infinite linear;
  -ms-animation: rotation 2s infinite linear;
  animation: rotation 2s infinite linear;
}

@-webkit-keyframes rotation {
  from {
      -webkit-transform: rotate(0deg);
  }
  to {
      -webkit-transform: rotate(359deg);
  }
}
@-moz-keyframes rotation {
  from {
      -moz-transform: rotate(0deg);
  }
  to {
      -moz-transform: rotate(359deg);
  }
}
@-ms-keyframes rotation {
  from {
      -ms-transform: rotate(0deg);
  }
  to {
      -ms-transform: rotate(359deg);
  }
}
@keyframes rotation {
  from {
      transform: rotate(0deg);
  }
  to {
      transform: rotate(359deg);
  }
}
// -----------------------------------------


.md-geometry-circle-map {
  stroke: $watermelon;
  stroke-width: 0.3px;
  fill: #fff;
  transition: all .2s ease-in-out;
}
.md-geometry-circle-map:hover {
  // stroke: #c1262e;
  stroke: $watermelonHover;
  stroke-width: 0.4px;
  fill: #fefefe;
}

.md-geometry-circle-selected {
  fill: $watermelon;
  stroke-width: 2px;
  stroke: $watermelon;
  transition: all .2s ease-in-out;
}

.md-geometry-circle-selected:hover {
  stroke-width: 4px;
}

// in map on oil field page
.popoverContainer{
  color: $battleship;
}


