// plot axes

.y-axis-max{
  font-size: 10px;
  position: absolute;
  left: 0px;
  top: -1rem;
}
.y-axis-min{
  font-size: 10px;
  position: absolute;
  left: 0px;
  bottom: -1rem;
}
.x-axis-max{
  font-size: 10px;
  position: absolute;
  bottom: -1rem;
  right: 10px;
}


// ==========================================
// Notification
// ==========================================

.notification-card{
  position:relative;
  width: 368px;
  // height: 183px;
  border-radius: 1px;
  padding: 0px 16px 32px 16px;
  background-color: #ffffff;
  box-shadow: 0 5px 8px 0 rgba(131, 131, 143, 0.08), 0 3px 6px 0 rgba(62, 68, 167, 0.04);
}

.notification-card-section{
  margin-bottom: 8px;
}

.notification-arrow{
  color: $watermelon;
}

.notification-close-button{
  display: flex;
  flex-direction: row;
  align-items: center;

  button: {
    width: 24px;
    height:24px;
  }
}

.notification-value{
  font-size: 14px;
  display: flex;
  flex-direction: row;
  align-items: center;
  svg: {
    margin-right: 5px;
  }
}

.notification-footer{
  height: 32px;
  border-radius: 1px;
  background-color: $veryPale;
  position: absolute;
  bottom: 0px;
  left: 0px;
  right: 0px;
  padding: 0px 20px 0px 20px;
  font-size: 11px;
  color: $battleship;
}

.notification-footer-text{
  font-size: 11px;
  line-height: 1.45;
  span {
    margin: 0;
    margin-right:16px;
  }
}




